import React, { Component } from 'react';
import { CollapseButton, DropdownAppend } from './Utils.js';
import withCollapse from './withCollapse.js';
import { toast } from 'react-toastify';

import { FilePond, registerPlugin } from 'react-filepond';
import FilePondPluginFileValidateType from 'filepond-plugin-file-validate-type';

registerPlugin(FilePondPluginFileValidateType)

const Bundle = props => (
  <div className="card text-white mb-4">
    <div className="card-header">
      <h4 className="flex-header">
        <span>Request Bundle TX <sub><small><i className="fas fa-exclamation-triangle"></i> Unstable Feature</small></sub></span>
        <CollapseButton
          onCollapse={props.onCollapse}
          collapsed={props.collapsed}
        />
      </h4>
    </div>  
    <div className={"card-body" + (props.collapsed ? " card-collapse" : "")}>
      <BundleInput
        {...props}
      /> 
    </div>
  </div>
);

const BundleInput = props => (
  <form>
    <div className="form-group">
      <label>Reward</label>
      <i data-tip="The reward implicitly includes the network transaction fee." className="fas fa-info-circle ml-2"></i>
      <div className="input-group">
        <input
          onFocus={(e) => e.target.select()}
          onChange={(e) => props.onInputChange({...props.inputData, reward: e.target.value})}
          className="form-control text-monospace"
          id="reward"
          value={props.inputData.reward}
        />
        <DropdownAppend
          className="disabled"
          symbols={props.symbols}
          currency={props.inputData.currency}
          setCurrency={props.setCurrency}
        />
      </div>
      <small id="rwHelp" className="form-text text-gray">Amount awarded to the address that fulfills your request.</small>
    </div>
    <div className="form-group">
      <label>Blocks</label>
      <i data-tip="Blocks till your TX is available for mining. One block is mined every ~15 seconds." className="fas fa-info-circle ml-2"></i>
      <div className="input-group">
        <input
          onFocus={(e) => e.target.select()}
          onChange={(e) => props.onInputChange({...props.inputData, blocks: e.target.value === '' ? '0' : e.target.value})}
          className="form-control text-monospace"
          id="blocks"
          value={props.inputData.blocks}
        />
      </div>
      <small id="bHelp" className="form-text text-gray">Blocks to wait before the transaction is broadcasted.</small>
    </div>

    <FilePond
      ref={props.setPond}
      instantUpload={false}
      dropValidation
      acceptedFileTypes={['text/csv']}
    />
    <button
      onClick={(e) => props.onBundle(e)}
      className="border-dark btn btn-block btn-lg btn-primary rounded-0"
    >
      Sign and Schedule
    </button>
  </form>
);

class SendBundleContainer extends Component {
  constructor(props){
    super(props);

    this.symbols = ['ETH','USD'];
    this.state = {reward: "0", blocks: "10", currency: this.symbols[0]};
    
    props.onCollapse();
  }

  render() {
    return (
      <Bundle
        {...this.props}
        onInputChange={this.onInputChange.bind(this)}
        onBundle={this.onBundle.bind(this)}
        inputData={this.state}
        symbols={this.symbols}
        setCurrency={this.setCurrency.bind(this)}
        setPond={this.setPond.bind(this)}
      />
    );
  }
  
  setCurrency(c) {
    this.setState({currency: c});
  }

  setPond(ref) {
    this.pond = ref;
  }

  onBundle(e) {
    e.preventDefault();

    const f = this.pond.getFiles();
    if(f.length === 0) {
      toast.error("No CSV file attached.");
    } else {
      const Papa = require("papaparse/papaparse.min.js");
      Papa.parse(f[0].file, {
        skipEmptyLines: 'greedy',
        complete: (b) => this.props.onBundle(b, this.state) 
      });
    }
  }

  onInputChange(newState) {
    if((/(^[0-9][0-9]{0,5}$)|(^$)/.test(newState.blocks))
      && (/(^[0-9]+\.?[0-9]{0,18}$)|(^$)/.test(newState.reward)))
        this.setState(newState);
  }
}

export default withCollapse(SendBundleContainer);
